<template>
  <div class="payIndex">
    <div v-if="parseInt(role) === 1" class="header">
      <el-dropdown @command="chose" style="font-size: 16px;padding-right: 40px;">
        <span class="el-dropdown-link cursor-pointer">转账制单<i class="el-icon-arrow-down el-icon--right"></i></span>
        <el-dropdown-menu slot="dropdown" size="medium">
          <el-dropdown-item command="pay-payee">单笔付款</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <router-link :to="'/main/pay/pay-account'" class="router-link-class">常用账户管理</router-link>
    </div>
    <div v-else-if="parseInt(role) === 2" class="header">
      <el-dropdown @command="chose" style="font-size: 16px;padding-right: 40px;">
        <span class="el-dropdown-link cursor-pointer">转账复核<i class="el-icon-arrow-down el-icon--right"></i></span>
        <el-dropdown-menu slot="dropdown" size="medium">
          <el-dropdown-item command="pay-check">按笔复核</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'PayIndex',
  computed: {
    role () {
      return this.$store.state.role
    }
  },
  methods: {
    chose (command) {
      switch (command) {
        case 'pay-payee':
          this.goRouter('/main/pay/pay-transfer')
          break
        case 'pay-payee-group':
          this.goRouter('/main/pay/pay-payee-group')
          break
        case 'pay-check':
          this.goRouter('/main/pay/pay-check')
          break
      }
    },
    goRouter (path) {
      this.$router.push(path)
    }
  }
}
</script>

<style lang="scss" scoped>
  .payIndex {
    .router-link-active {
      position: relative;
      color: #1f93d5;
    }
    .header {
      border: 1px solid #c4c4c4;
      width: 100%;
      height: 40px;
      background-color: #ffffff;
      padding: 0 35px;
      font-size: 14px;
      color: #666666;
      display: flex;
      align-items: center;
      .router-link-class {
        font-size: 15px;
        color: #1f93d5;
        font-weight: 700;
      }

      .el-dropdown{
        .el-dropdown-link{
          color: #1f93d5;
          background:#ffffff;
          border: none;
          font-weight: 700;
        }
      }
    }
  }

</style>
